.outliner-list {
  padding: 50px 0;
}
.outliner-list-item {
  height: 40px;
  max-width: 800px;
  margin: 0 auto;
  background: #fff;
  box-shadow: 1px 1px 1px #fff;
  display: -webkit-box;
  display: flex;
  -webkit-box-align: center;
          align-items: center;
  padding: 0 20px;
}
.outliner-list-item:hover {
  cursor: pointer;
  color: #3f98e5;
}
.outliner-list-item + .outliner-list-item {
  margin-top: 10px;
}
.outliner-list-item-title {
  width: 400px;
}
