.bullet-point {
  position: relative;
  padding: 2px 0 0;
  z-index: 1;
  font-size: 16px;
}
.bullet-point-debug {
  background-color: #9bb65c;
  position: absolute;
  padding: 0 10px;
  color: #ffffff;
  height: 18px;
  font-size: 14px;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  left: -20px;
  top: 5px;
}
.bullet-point-action {
  width: 25px;
  height: 20px;
  float: left;
  color: #333;
  position: relative;
  z-index: 5;
  padding-top: 3px;
}
.bullet-point-action-handle {
  width: 20px;
  height: 20px;
  line-height: 20px;
  display: inline-block;
  float: left;
  text-align: center;
  font-size: 16px;
  border-radius: 100%;
  cursor: pointer;
  font-family: Arial, Helvetica, Verdana, Tahoma, sans-serif;
}
.bullet-point-action-handle:hover {
  background: #c1c1c1;
  color: #fff;
}
.bullet-point-action-handle::after {
  font-size: 24px;
  content: "•";
  color: rgba(0, 0, 0, 0.6);
  display: inline-block;
}
.bullet-point-action-handle-expand::after {
  content: "－";
  font-size: 14px;
}
.bullet-point-action-handle-collapse::after {
  font-size: 18px;
  content: "+";
}
.bullet-point-content {
  position: relative;
  margin-left: 25px;
  z-index: 5;
  padding-top: 2px;
}
.bullet-point-children {
  margin-top: 2px;
  margin-left: 10px;
  padding-left: 20px;
  position: relative;
  z-index: 5;
  border-left: 1px solid #eee;
}
